import * as $ from 'jquery';
import './PolyFills';
import YoutubePlayer from "./components/YoutubePlayer";
import FormSection from "./sections/FormSection";
import app from "./components/AddressSelectList"

var utube;
var isPlaying = false;
$(function() {

  var formNum = 0;

  $('.menu').on("click", function() {
    $('.menu-btn').trigger("click");
  })

  $(window).scroll(function() {
    var scrollVal = $(this).scrollTop();
    // console.log(scrollVal, $('#video').offset().top);
    if( scrollVal < ($('#video').offset().top+ window.innerHeight) && scrollVal > ($('#video').offset().top - window.innerHeight/2) ){
      if(isPlaying == false){
        console.log('play');
        utube.playVideo();
        isPlaying = true;
      }else{

      }

    }else{
      if( isPlaying == true){
        console.log('stop');
        utube.pauseVideo();
        isPlaying = false;
      }
    }
  });
  createYoutubePlayer();
});

function windowResizeHandler(e) {
}
function createYoutubePlayer(){
  utube = new YoutubePlayer();
  utube.emitter.on('ON_YOUTUBE_API_READY', () => {
      utube.videoId = 'OOG1Lx-ICc0';
      utube.loop = true;
      utube.mute = false;
      utube.createPlayer('youtubePlayer');
  });
  utube.emitter.on('YOUTUBE_PLAYER_READY', () => {
      // this.youtubePlayer.playVideo();
  });
  utube.loadYoutubeAPI();
}
